/* UnifiedTimelineChart.css */
.unified-timeline-chart {
  width: 100%;
  margin-bottom: 2rem;
}

.chart-container {
  position: relative;
  height: 500px;
  width: 100%;
  margin-bottom: 2rem;
  background: #ffffff;
  border-radius: 12px;
  padding: 1.5rem;
  box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1), 0 2px 4px -1px rgba(0, 0, 0, 0.06);
  border: 1px solid rgba(0, 0, 0, 0.05);
}

.chart-analysis {
  background: linear-gradient(135deg, #f8fafc 0%, #e2e8f0 100%);
  border-radius: 12px;
  padding: 2rem;
  border: 1px solid #e5e7eb;
}

.chart-analysis h3 {
  margin: 0 0 1.5rem 0;
  color: #1f2937;
  font-size: 1.5rem;
  font-weight: 600;
  text-align: center;
}

.analysis-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 1.5rem;
  margin-bottom: 2rem;
}

.election-stats {
  background: #ffffff;
  border-radius: 8px;
  padding: 1.5rem;
  box-shadow: 0 2px 4px -1px rgba(0, 0, 0, 0.1);
  border: 1px solid rgba(0, 0, 0, 0.05);
}

.election-stats h4 {
  margin: 0 0 1rem 0;
  color: #1f2937;
  font-size: 1.1rem;
  font-weight: 600;
  text-align: center;
  padding-bottom: 0.5rem;
  border-bottom: 2px solid #e5e7eb;
}

.stats-row {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 0.5rem;
}

.stat-label {
  font-size: 0.9rem;
  color: #6b7280;
  font-weight: 500;
}

.stat-value {
  font-size: 1rem;
  color: #1f2937;
  font-weight: 700;
}

.insights {
  background: #ffffff;
  border-radius: 8px;
  padding: 1.5rem;
  box-shadow: 0 2px 4px -1px rgba(0, 0, 0, 0.1);
  border: 1px solid rgba(0, 0, 0, 0.05);
}

.insights h4 {
  margin: 0 0 1rem 0;
  color: #1f2937;
  font-size: 1.1rem;
  font-weight: 600;
}

.insights ul {
  margin: 0;
  padding-left: 1.5rem;
}

.insights li {
  margin-bottom: 0.5rem;
  color: #4b5563;
  line-height: 1.5;
}

.insights li strong {
  color: #1f2937;
}

/* Color indicators for elections */
.election-stats:nth-child(1) h4::before { content: "●"; color: #ef4444; margin-right: 0.5rem; }
.election-stats:nth-child(2) h4::before { content: "●"; color: #f97316; margin-right: 0.5rem; }
.election-stats:nth-child(3) h4::before { content: "●"; color: #eab308; margin-right: 0.5rem; }
.election-stats:nth-child(4) h4::before { content: "●"; color: #22c55e; margin-right: 0.5rem; }
.election-stats:nth-child(5) h4::before { content: "●"; color: #3b82f6; margin-right: 0.5rem; }

/* Responsive design */
@media (max-width: 768px) {
  .chart-container {
    height: 400px;
    padding: 1rem;
  }
  
  .chart-analysis {
    padding: 1.5rem;
  }
  
  .analysis-grid {
    grid-template-columns: 1fr;
    gap: 1rem;
  }
  
  .election-stats {
    padding: 1rem;
  }
  
  .insights {
    padding: 1rem;
  }
}

@media (max-width: 480px) {
  .chart-container {
    height: 300px;
    padding: 0.5rem;
  }
  
  .chart-analysis {
    padding: 1rem;
  }
  
  .chart-analysis h3 {
    font-size: 1.25rem;
  }
}
